<template>
  <div>
    <h4>Key Metrics</h4>
    <div>
        <DeptChart :deptData="deptTotals" :deptLabels="deptLabels"></DeptChart>
        <TypeChart :employeeData="[this.employeeTypes['full-time'], this.employeeTypes['contract']]"></TypeChart>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit.js'
import DeptChart from './DeptChart'
import TypeChart from './TypeChart'
export default {
    name: 'employees',
    components: {
        DeptChart,
        TypeChart
    },
    data() {
        return {
            employees: [],
            employeeTypes: {
                'full-time': null,
                'contract': null,
            },
            deptLabels: [],
            deptTotals: []
        }
    },
    created () {
        db.collection('employees').orderBy('dept').get()
            .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'employee_id': doc.data().employee_id,
                    'name': doc.data().name,
                    'dept': doc.data().dept,
                    'position': doc.data().position,
                    'type': doc.data().type
                }
                this.employees.push(data)
            })
            this.employeeTypes['full-time'] = this.employees.filter(obj => obj.type === 'full-time').length;
            this.employeeTypes['contract'] = this.employees.filter(obj => obj.type === 'contract').length;
            
            let deptData = {}
            this.employees.forEach(obj => {
                deptData[obj.dept] ? deptData[obj.dept]++ : deptData[obj.dept] = 1;
            })
            this.deptLabels = Object.keys(deptData)
            this.deptTotals = Object.values(deptData)
        })
    }
}

</script>


