<template>
    <div id="employees">
        <span><h4>Current Employees </h4><p><em>({{employeeTotal}} total)</em></p></span>
        <ul class="collection width-header">
            <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
                <div class="chip yellow lighten-2">{{ employee.dept}}</div>
                {{ employee.name }}
                <router-link class="secondary-content"
                v-bind:to="{name: 'view-employee', params: {employee_id: employee.employee_id}}">
                <i class="fas fa-info-circle inline-icon"></i>
                </router-link>
                </li>
        </ul>
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit.js'
export default {
    name: 'employees',
    data() {
        return {
            employees: [],
            employeeTotal: '',
        }
    },
    created () {
        db.collection('employees').orderBy('dept').get()
            .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                // console.log(doc.data());
                const data = {
                    'id': doc.id,
                    'employee_id': doc.data().employee_id,
                    'name': doc.data().name,
                    'dept': doc.data().dept,
                    'position': doc.data().position,
                    'type': doc.data().type
                }
                this.employees.push(data)
            })
            this.employeeTotal = this.employees.length
        })
    }
}

</script>

<style scoped>

span {
    display: flex;
}

p {
    color: gray;
    line-height: 47px;
    font-size: 18px;
    vertical-align: bottom !important;
    padding-left: 5px;
}

.inline-icon {
   vertical-align: bottom !important;
   font-size: 25px !important;
   line-height: 35px;
   color: rgb(0, 195, 255);
}

</style>


