<template>
    <div>
    <nav>
        <div class="nav-wrapper blue darken-2">
            <router-link to="/" class="brand-logo left"><i class="material-icons left">people_outline</i>
Argus HR</router-link>
            <ul class="right">
                <li><router-link v-if="!isLoggedIn" to="/login">Login</router-link></li>
                <li><router-link v-if="!isLoggedIn" to="/signup">Signup</router-link></li>
                <li><router-link v-if="isLoggedIn" to="/" class="hide-on-small-and-down">Employees</router-link></li>
                <li><router-link v-if="isLoggedIn" to="/metrics" class="hide-on-small-and-down">Key Metrics</router-link></li>
                <li v-if="isLoggedIn"><span class="email grey-text text-lighten-1 hide-on-med-and-down">{{ currentUser }}</span></li>
                <li><button v-if="isLoggedIn" @click="logout" class="btn-flat">logout</button></li>
            </ul>
        </div>
    </nav>
    </div>
</template>

<script>
import firebase from 'firebase'
export default {
    name: 'navbar',
    data() {
        return {
            isLoggedIn: false,
            currentUser: false
        }
    },
    created() {
        if (firebase.auth().currentUser) {
            this.isLoggedIn = true
            this.currentUser = firebase.auth().currentUser.email
        }
    },
    methods: {
        logout() {
            firebase
            .auth()
            .signOut()
            .then(() => this.$router.push('/login'))
        }
    }    
}
</script>

<style scoped>

.brand-logo {
    padding: 0 15px;
}

.brand-logo i {
    font-size: 45px;
    color: rgb(241, 241, 163);
}

.email {
    padding: 0 10px;
}

</style>


