<template>
<div class="card hoverable grey lighten-5">
    <canvas id="deptChart"></canvas>
</div>
</template>

<script>
export default {
    name: 'dept-chart',
    data() {
        return {
            loaded: false
        }
    },
    props: ['deptData', 'deptLabels'],
    watch: {
        deptData: function (newVal, oldVal) {
            if (newVal.length > 0 && !this.loaded) {
                this.loaded = true;
                this.createChart('deptChart', this.deptData, this.deptLabels);
            }
        }
    },
    methods: {
        createChart(chartId, chartData, chartLabels) {
            const ctx = document.getElementById(chartId);
            const myChart = new Chart(ctx, {
            type: 'bar',
            data: {
              labels: chartLabels,
              datasets: [{
                  label: '# of employees',  
                  data: chartData,
                  backgroundColor: '#64b5f6', 
                },
              ]
            },
            options: {
              title: {
                  display: true,
                  position: 'top',
                  text: 'Employees by Department',
                  fontSize: 25,
                  fontColor: 'black',
                  padding: 20
              },
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero: true
                        }
                    }]
                }
            }
        });
      }
    },
}

Chart.defaults.global.defaultFontFamily = "Roboto"

</script>

<style>
    
    canvas {
        margin: 60px 0;
    }

</style>


