<template>
<div class="card hoverable grey lighten-5">
    <canvas id="typeChart"></canvas>
</div>
</template>

<script>
export default {
    name: 'type-chart',
    data() {
        return {
            loaded: false
        }
    },
    props: ['employeeData'],
    watch: {
        employeeData: function (newVal, oldVal) {
            if (newVal.length > 0 && !this.loaded) {
                this.loaded = true;
                this.createChart('typeChart', this.employeeData);
            }
        }
    },
    methods: {
        createChart(chartId, chartData) {
            const ctx = document.getElementById(chartId);
            const myChart = new Chart(ctx, {
            type: 'doughnut',
            data: {
              labels: ['Full-time', "Contract"],
              datasets: [
                {
                  data: chartData,
                  backgroundColor: ['#bbdefb', '#fbc02d '] 
                },
              ]
            },
            options: {
              title: {
                  display: true,
                  position: 'top',
                  text: 'Full-time vs. Contract employees',
                  fontSize: 25,
                  fontColor: 'black',
                  padding: 20
              },
              legend: {
                display: true,
                position: 'bottom',
                labels: {
                    fontColor: 'black'
                }
              },
              responsive: true,
              scales: {
                xAxes: [{
                  gridLines: {
                      display:false,
                      drawBorder: false
                  },
                  ticks: {
                          display: false //this will remove only the label
                  }
                }],
                yAxes: [{
                  gridLines: {
                      display:false,
                      drawBorder: false
                  },
                  ticks: {
                          display: false //this will remove only the label
                  } 
                }]
              }
            }
        });
      }
    },
}

Chart.defaults.global.defaultFontFamily = "Roboto"

</script>


